<template>
  <v-card class="login-page">
    <div>
      <img src="../assets/logo.png">
      <p class="login-title">用户登录</p>
      <v-alert type="error" v-if="showErrTip" :message="ErrTip" style="margin:10px 80px;color:red;"></v-alert>
      <v-form direction="horizontal" class="login-main">
          <v-form-item label="账号" :label-col="labelCol" :wrapper-col="wrapperCol">
              <v-input type="text" placeholder="请输入账号" v-model="req.account"
              size="large" class="login-acntInput"></v-input>
              <v-button @click="toSignup()">注册</v-button>
          </v-form-item>
          <v-form-item label="密码" :label-col="labelCol" :wrapper-col="wrapperCol">
              <v-input type="password" placeholder="请输入密码" v-model="req.password" size="large"
              @keyup.enter.native="LogIn()"></v-input>
          </v-form-item>
          <v-form-item>
              <v-button type="primary" @click.prevent="LogIn()">登录</v-button>
          </v-form-item>
      </v-form>
    </div>
  </v-card>
</template>

<script>
export default {
  data () {
    return {
      req: {
        account: "",
        password: ""
      },
      labelCol: {
        span: 6
      },
      wrapperCol: {
        span: 14
      },
      showErrTip: false,
      ErrTip: ""
    }
  },
  methods: {
    toSignup() {
      this.$router.push({path: '/Signup'});
    },
    LogIn() {
      if(this.req.account == "" || this.req.password == "" ) {
        this.$message['error']("请输入账号及密码");
        return false;
      }
      let api = '/api/login';
      this.$http.post(api, this.req).then(res => {
        if(!res.data.code) {
          let logmsg = {
            isLog: true,
            account: this.req.account,
            nickname: res.data.nickname,
            coins: res.data.goalCoin
          }
          this.$store.commit('setLogMsg', logmsg);
          this.$router.push({path: '/'});
        } else if (res.data.code == 1){
          this.showErrTip = true;
          this.ErrTip = "无此用户";
        } else if (res.data.code == 2) {
          this.showErrTip = true;
          this.ErrTip = "密码错误";
        } else {
          this.showErrTip = true;
          this.ErrTip = "未知错误";
        }
      }).catch(err => {
        console.log(err);
        this.showErrTip = true;
        this.ErrTip = "网络错误";
      });
    }
  }
}
</script>

<style scoped>
.login-page {
  width: 100%;
  height: 100%;
  display:flex;
  justify-content:center;
  align-items:center;
}

.login-title {
  font-size: 28px;
  font-weight: bold;
  margin: 12px 0;
}

.login-acntInput {
  width: 70%;
  margin-right: 24px;
}
</style>



